.footer {
    background: #ddd;
    height: auto;
    width: 100%;
    position: relative;


    .foonter_box {
        display: flex;

        flex-direction: column;
        text-align: center;

        p {
            font-size: 1rem;
            padding-top: 1rem;

            &:last-child {
                padding-bottom: 1rem;
            }

            @media screen and (max-width:700px) {
                font-size: .5rem;

            }
        }

    }
}