.ranking_posts {
    .title {
        font-size: 3rem;
        padding: 5rem 0;
        text-align: center;

        @media screen and (max-width:800px) {
            padding: 1.8rem 0;
        }
    }

    .ranking_box {
        display: flex;
        width: 100%;

        .ranking_table {
            flex: 70%;

            display: flex;
            flex-direction: column;
            margin-bottom: 100px;

            .ranking_table_top {
                background: #ddd;
                border-radius: 20px;

                .ranking_img {
                    flex: 10%;
                    font-size: 1rem;

                    @media screen and (max-width:800px) {
                        font-size: .7rem;
                        flex: 20%;
                    }
                }

                .ranking_title {
                    text-align: center;
                }
            }

            li {
                border-top: 2px solid #ddd;
                display: flex;
                align-items: center;

                padding: 1rem 0;
                justify-content: center;

                &:nth-child(1) {
                    border-top: none
                }

                &:nth-child(2) {
                    border-top: none
                }

                .num {
                    flex: 10%;
                    text-align: center;
                    font-size: 1rem;

                    @media screen and (max-width:800px) {
                        font-size: .7rem;
                        display: none;
                    }
                }

                .ranking_img {
                    flex: 10%;
                    text-align: center;

                    @media screen and (max-width:800px) {

                        flex: 20%;
                    }

                    img {
                        width: 100%;
                        height: 100px;
                        border-radius: 10px;
                    }
                }

                .ranking_title {
                    flex: 32%;
                    padding-left: 3rem;
                    font-size: 1rem;
                    box-sizing: border-box;

                    @media screen and (max-width:800px) {
                        font-size: .7rem;
                        padding-left: 1rem;
                    }

                    &:hover {
                        text-decoration: underline;
                    }
                }

                .ranking_nutriment {
                    flex: 16%;
                    text-align: center;
                    font-size: 1rem;

                    @media screen and (max-width:800px) {
                        font-size: .7rem;
                    }
                }

                .food_tag {
                    flex: 16%;
                    text-align: center;
                    font-size: 1rem;

                    @media screen and (max-width:800px) {
                        font-size: .7rem;
                    }
                }

                .food_type {
                    flex: 16%;
                    text-align: center;
                    font-size: 1rem;

                    @media screen and (max-width:800px) {
                        font-size: .7rem;
                    }
                }
            }
        }

        .ranking_category {
            flex: 20%;
        }
    }
}